#add_rule_modal{
    position: relative;
    width: 700px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    border: 1px solid #ccc;
    border-radius: 2px;
}

.add_rule_info{
    font-size: 16px;
    color: #999;
    margin: 0;
}

#tasks_header{
    display: flex;
    align-items: center;
    gap: 10px;
}



#added_tasks{
    border-bottom: 1px solid #ccc;
}

#deploy{
    border: 1px solid #ccc;
    background: #eee;
    margin-top: 20px;
 
}

#deploy:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}