#replicate_data_modal{
    position: relative;
    width: 500px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    border: 1px solid #ccc;
}
.replicate_data_title span{
    font-weight: 400;
}

.replicate_data_info{
    font-size: 12px;
    margin: 0;
    color: #999;
}

#replicate_data_ip{
    width: 250px;
    height: 25px;
    padding: 5px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 2px;
}

#replicate_data_btn{
    margin-top: 20px;
    border: 1px solid #ccc
}

#replicate_data_btn:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}