#view_data_modal{
    position: relative;
    width: 600px;
    min-height: 200px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 50px;
    padding-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 2px;
}

#view_data_header{
    width: 100%;
    height: 50px;
    background: #474787;
    margin: 0;
}

#view_data_inheader{
    width: 90%;
    height: 100%;
    display: flex;
    align-items: center;
    margin: 0 auto;
   
}

#view_data_title{
    color: white;
}

#view_data_main{
    width: 90%;
    display: block;
    margin: 0 auto;
    padding: 1px;
}

.view_data_value{
    font-size: 18px;
}

#view_data_btns{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}

#save_doc{
    border: 1px solid #ccc;
    background: #eee;

}

#save_doc:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

#delete_doc{
    border: 1px solid #f5613d;
    background: #eee;

}

#delete_doc:hover{
    cursor: pointer;
    background: #e84118;
    color: white;
}