

*:focus{
    outline: none;
    
}
body{
    background: #f0f0f0;
}

a{
    color:#474787;
}

button{
    height: 30px;
    font-size: 16px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    padding: 0px 8px;
    gap: 5px;
}

select:hover{
    cursor: pointer;
}

.folder{
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 100px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

.folder:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

.folder:hover .folder_name{
   text-decoration: underline;
}

.folder_container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
}

.folder_image{
    width: 50px;
    height: 50px;
    display: block;
    margin: 0 auto;
    background: #3d3d75;
    border-radius: 3px;
}

.folder_name{
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 20px;
}

.folder_docs{
    padding: 0;
    margin: 0;
    text-align: center;
}

.folder_docs span{
    font-weight: 500;
}

/* split this */
.second-modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 101;
}

.modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 10;
}
.modal_title{
    color: black;
}
.modal_container{
    padding: 10px;
    width: 90%;
    display: block;
    margin: 0 auto;
}
