#view_recent_query_modal{
    width: 600px;
    max-height: 80vh;
    padding: 20px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 50px;
    overflow-y: auto;
}

.recent_query_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
}

.view_recent_query_info{
    margin: 0;
    color: #999;
}

.recent_query{
    width: 75%;
    display: flex;
    justify-content: space-between;
    background: #eee;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 3px;
}

.recent_query p:nth-child(1){
    width: 60%;
}

.recent_query:hover{
    border: 1px solid #bbb;
    cursor: pointer;
}

.run_recent_query{
    border: 1px solid #ccc;
}

.run_recent_query:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}