#profile{
    margin-top: 20px;
}

#profile_header{
    
    display: flex;
    align-items: center;
    gap: 10px;
}
#profile_pic{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: gold;
}

#upgrade{
    margin-top: 20px;
    border: 2px solid #3d3d75;
}
#upgrade:hover{
    cursor: pointer;
    background: #3d3d75;
    color: white;
}