.header{
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #ccc;
    background: #3d3d75;
    position: fixed;
    z-index: 1;
    color: white;
}
#header_container{
    height: 100%;
    min-width: 650px;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}
#header_container_left{
    display: flex;
    align-items: center;
    height: 100%;
}
#logo{
    font-size: 35px;
}
#logo:hover{
    cursor: pointer;
}
#menu{
    height: 30px;
    display: flex;
    align-items: center;
    margin-left: 40px;
    background: #28284d;
    border: 1px solid #1e1e3b;
    border-radius: 3px;    
}
.menu_item{
    height: 30px;
    display: flex;
    align-items: center;
    gap: 1px;
    padding: 0 5px;
    user-select: none; 
}

.menu_item:hover{
    cursor: pointer; 
    background:#141426;   
}
.menu_item_selected{
    background: #141426;
}

.menu_image{
    font-weight: 500;
    font-weight: 500;
    font-size: 16px;
}

.menu_text{
    font-size: 16px;
    font-weight: 400;
}

#need_help{
    background: #3d3d75;
    border: 1px solid #4c4c91;
    color: white;
}
#need_help:hover{
    cursor: pointer;
    background:#4c4c91;
}