#add_task_modal{
    background: white;
    border: 1px solid #ccc;
    border-radius: 2px;
    width: 600px;
    height: 350px;
    display: block;
    margin: 0 auto;
    margin-top: 40px;
}
.add_task_info{
    font-size: 12px;
    margin: 0;
    color: #999;
}

.add_task_info span{
    color: black;
    font-weight: 500;
}

#task_box_desc{
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
}

h2{
    font-weight: 500;
}

#task_box{
    display: flex;
    align-items: center;
    gap: 10px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
}

#rule_type_desc{
    width: 100px;
}

#rule_input_desc{
    width: 320px;
}

.task{
    border: 1px solid #ccc;
    background: #eee;
    padding: 4px;
} 

.rule_select{
    width: 100px;
    height: 30px;
}

#rule_input{
    width: 300px;
    height: 60px;
    resize: none;
    padding: 10px;
    font-size: 16px;
}

#add_task{
    border: 1px solid #ccc;
    background: #eee;
    color: black;
    margin-top: 10px;
}

#add_task:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}
