
#back_section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 160px;
}

.recent_queries{
    color:#474787;
    text-decoration: underline;
}
.recent_queries:hover{
    cursor: pointer;
}

#query_txt_container h1{
    font-size: 16px;
}
#query_txt{
    font-weight: 500;
    font-size: 16px;
    width: 100%;
    height: 80px;
    resize: none;
    box-sizing: border-box;
    padding: 20px;
    padding-top: 10px;
    border: 1px solid #bbb;
    border-radius: 3px;
}

#query_btn{
    border: 1px solid #ccc;
    background: #eee;
}

#query_btn:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

#query_results_title{
    font-size:20px;
    margin-top: 30px;
}