#add_data_modal{
    width: 400px;
    height: 420px;
    border-radius: 2px;
    border: 1px solid #aaa;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    
}
#add_data_modal_container{
    width: 90%;
    display: block;
    margin: 0 auto;
}

/* make a class */
#add_data_folder:nth-child(2){
    margin-top: -10px;
}

.add_data_info{
    margin: 0;
    margin-top: 5px;
    font-size: 12px;
    color: #aaa;
}

#add_data_folder{
    width: 314px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}

#add_data_key{
    width: 300px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}

#add_data_key::placeholder{
    color: black;
}

#add_data_value{
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    padding-top: 5px;
    resize: none;
}

#add_data_file{
    display: none;
}

#attach_file{
    width: 100px;
    height: 28px;
    border: 1px solid #ccc;
    border-radius: 2px;
    background: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

#attach_file:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

#attach_file #user_profile{
    background: #3498db;
}

#add_data_btn{
    border: 1px solid #ccc;
    background: #eee;
    margin-top: 30px;
}
#add_data_btn:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}